.tui-pagination{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px 0 60px;
}
.tui-page-btn{
    margin: 0 2px;
    padding: 8px;
    background-color: #fff;
    border: none;
    color: #000;
    cursor: pointer;
    font-family: Roboto;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.333;
}
.tui-is-selected{
    width: 40px;
    height: 40px;
    border-radius: 5px;
    background-color: #FF6B08;
    color: #fff;
}
.tui-ico-prev{
    background-image: url();
}